<template>
  <table class="table table-striped mx-auto w-50 mt-3">
    <thead>
      <tr class="table-warning">
        <th>ФИО</th>
        <th>Предложение или жалоба</th>
        <th>Дата отправки</th>
      </tr>
    </thead>

    <tbody>
      <tr v-if="request.data.length === 0">
        <td colspan="3">Загрузка данных...</td>
      </tr>
      <tr class="table-info" v-else v-for="item in request.data" :key="item.id">
        <td>{{ item.full_name }}</td>
        <td>{{ item.suggestion }}</td>
        <td>{{ item.date }}</td>
      </tr>
</tbody>

  </table>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      request: { data: [] }
    };
  },
  async mounted() {
    await this.getProps();
  },
  methods: {
    async getProps() {
      try {
        const response = await axios.get('http://127.0.0.1:8000/api/');
        this.request = {...response};
      } catch (error) {
        if (error.response) {
          console.error('Response Error:', error.response.data);
          console.error('Response Status:', error.response.status);
        } else if (error.request) {
          console.error('Request Error:', error.request);
        } else {
          console.error('Error:', error.message);
        }
      }
    }
  }
};
</script>
