<template>
    <div class="form-control container my-3">
        <form @submit.prevent="sendForm">
            <label for="" class="label-form my-2">ФИО</label>
            <input v-model="FIO" class="form-control">

            <label for="" class="label-form my-2">Почта</label>
            <input type="email" v-model="email" class="form-control">

            <label for="" class="label-form my-2">Предложение или жалоба</label>
            <textarea v-model="suggestion" class="form-control"></textarea>

            <button type="submit" class="my-2 btn btn-success">Отправить!</button>
        </form>

        <!-- Модальное окно -->
        <div v-if="showModal" class="modal fade show" id="successModal" tabindex="-1" role="dialog" aria-labelledby="successModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="successModalLabel">Успешно отправлено</h5>
                        <button type="button" class="close" @click="closeModal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <p>Ожидайте ответа!</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            FIO: "",
            email: "",
            suggestion: "",
            showModal: false
        };
    },
    methods: {
        async sendForm() {
            const newProps = {
                FIO: this.FIO.trim(),
                email: this.email.trim(),
                suggestion: this.suggestion.trim(),
                date: Date.now(),
            };

            try {
                const response = await axios.post('http://127.0.0.1:8000/api/', newProps, {
                    headers: {
                        'Content-Type': 'application/json',
                    },
                });
                console.log(response.data);
                this.showModal = true;

                this.FIO = "";
                this.email = "";
                this.suggestion = "";
            } catch (error) {
                if (error.response) {
                    console.error('Response Error:', error.response.data);
                    console.error('Response Status:', error.response.status);
                } else if (error.request) {
                    console.error('Request Error:', error.request);
                } else {
                    console.error('Error:', error.message);
                }
            }
        },
        closeModal() {
            this.showModal = false;
        },
    },
};
</script>

