<template>
    <div class="outer">
        <div class="inner">
            <span>ФИО<strong>{{ $route.params.pk }}</strong></span>
            <p>Предложение или жалоба: </p>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            data: []
        }
    },
    mounted() {
        const objectId = this.$route.params.pk;
        axios.get(`http://127.0.0.1:8000/api/${objectId}/`)
            .then(response => {
                this.data = response.data;
                console.log(this.data);
            })

            .catch(error => {
                if (error.response) {
                    console.error('Response Error:', error.response.data);
                    console.error('Response Status:', error.response.status);
                } else if (error.request) {
                    console.error('Request Error:', error.request);
                } else {
                    console.error('Error:', error.message);
                }
            });
    }
}
</script>